import * as a1lib from "@alt1/base";
import Position from "./helpers/Position";
import ImageDataSet from "./helpers/ImageDataSet";
//tell webpack to add index.html and appconfig.json to output
require("!file-loader?name=[name].[ext]!./index.html");
require("!file-loader?name=[name].[ext]!./appconfig.json");
require("!file-loader?name=[name].[ext]!./icon.png");
var OVERLAY_GROUP = "Emotes";
var EMOTE_LIST = [
    "No",
    "Bow",
    "Angry",
    "Think",
    "Wave",
    "Shrug",
    "Cheer",
    "Beckon",
    "Laugh",
    "Jump For Joy",
    "Yawn",
    "Dance",
    "Jig",
    "Twirl",
    "Headbang",
    "Cry",
    "Blow Kiss",
    "Panic",
    "Raspberry",
    "Clap",
    "Salute",
    "Idea",
];
window.addEventListener("DOMContentLoaded", function () {
    setInterval(emotes, 2000);
});
var strips = a1lib.ImageDetect.webpackImages({
    strip: require("./strip.data.png"),
});
export var emotes = function () {
    var strip = strips.strip;
    var frame = a1lib.captureHoldFullRs();
    var color = a1lib.mixColor(255, 255, 255);
    var width = strip.width / EMOTE_LIST.length;
    var height = strip.height;
    var spacing = EMOTE_LIST.map(function () { return width; });
    var dataSet = ImageDataSet.fromFilmStripUneven(strip, spacing);
    var positions = dataSet.buffers.flatMap(function (buffer) { return frame.findSubimage(buffer); });
    document.getElementById("error").textContent = positions.length ? "" : "Tab is not visible";
    var normalizedYs = Position.normalizeYPositions(positions);
    window.alt1.overLayClearGroup(OVERLAY_GROUP);
    window.alt1.overLaySetGroup(OVERLAY_GROUP);
    window.alt1.overLayFreezeGroup(OVERLAY_GROUP);
    positions.forEach(function (position, index) {
        var size = 7;
        var title = EMOTE_LIST[index];
        var x = position.x + Math.round(width / 2);
        var y = Position.findClosest(normalizedYs, position.y) + height + Math.round(size / 2);
        window.alt1.overLayTextEx(title, color, size, x, y, 5000, "sans-serif", true, true);
    });
    window.alt1.overLayRefreshGroup(OVERLAY_GROUP);
};
if (window.alt1) {
    //tell alt1 about the app
    //this makes alt1 show the add app button when running insane the embedded browser
    //also updates app settings if they are changed
    window.alt1.identifyAppUrl("./appconfig.json");
}
